/* ==========================================================================
    GUIDE
  ===========================================================================
    WebP: Does not work on Safari 15. Only Big Sur+ will work.
    Must include <type="image/webp"> to make fallback working
    <type="image/jpeg"> is correct one, <type="image/jpg"> does not work on Safari.

    Responsive image treatment:

    !Only use WebP for large desktop screen.

    1. max-width: Mobile - Always @2x and JPG. Old iOS does not load WebP.
    2. min-width: Large desktop - Use WebP for large image
    3. <img>:     Fallback - Use high quality or @2x image. Only old browsers (and Safari) will load this.
  ========================================================================== */


/* ==========================================================================
    BOOTSTRAP GRID SYSTEM OVERRIDE
  ===========================================================================
  Default: Breakpoint  |  Content width
    sm:         576px  |  540px
    md:         768px  |  720px
    lg:         992px  |  960px
    xl:        1200px  |  1140px
    xxl:       1400px  |  1320px

  Add-on:
    3xl:       1800px  |  1400px
    4xl:       2500px  |  1600px
  ========================================================================== */

/* BREAKPOINT - !! this variabes does not replace BS default !! */
$breakpoint-xs:      375px; // Mobile portrait
$breakpoint-sm:      576px; // Mobile landscape
$breakpoint-md:      768px; // iPad
$breakpoint-lg:      992px; // iPad Pro
$breakpoint-xl:     1200px; // Laptop (*Trigger mobile menu below this)
$breakpoint-xxl:    1400px; // Desktop
$breakpoint-3xl:    1800px; // Add-on: HD 1920px
$breakpoint-4xl:    2400px; // Add-on: QHD 2560px

/* CONTENT WIDTH */
$content-width-sm:   100%; // Mobile     540px
$content-width-md:   720px; // iPad      720px
$content-width-lg:   960px; // iPad Pro  960px
$content-width-xl:  1140px; // Laptop   1140px
$content-width-xxl: 1140px; // Desktop  1320px
$content-width-3xl: 1140px; // Add-on:  HD 1400px
$content-width-4xl: 1140px; // Add-on:  QHD 1600px

/* CUSTOM GUTTER */
.container, .container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl, .row, .row>* {
  --bs-gutter-x: 1rem; //Default 24px (1.5rem)

  /* MOBILE */
  @media only screen and (min-width: 992px) {
    --bs-gutter-x: 1.5rem;
  }
  /* LARGE SCREEN */
  @media only screen and (min-width: $breakpoint-4xl) {
    --bs-gutter-x: 2rem;
  }
}


/* CONTAINER SIDE PADDING */
.container, .container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
  padding-right: calc(var(--bs-gutter-x) * 1); //Adds extra padding to container side
  padding-left:  calc(var(--bs-gutter-x) * 1);  //Adds extra padding to container side
}


/* CUSTOM CONTAINER WIDTH
  ========================================================================== */
//MD
@media only screen and (min-width: 768px) {
  .container, .container-fluid, .container-sm, .container-md {
    max-width: calc(#{$content-width-md} + (var(--bs-gutter-x) * 2));
  }
}

//LG
@media only screen and (min-width: 992px) {
  .container, .container-fluid, .container-sm, .container-md, .container-lg {
    max-width: calc(#{$content-width-lg} + (var(--bs-gutter-x) * 2));
  }
}

//XL
@media only screen and (min-width: 1200px) {
  .container, .container-fluid, .container-sm, .container-md, .container-lg, .container-xl {
    max-width: calc(#{$content-width-xl} + (var(--bs-gutter-x) * 2));
  }
}

//XXL
@media only screen and (min-width: 1400px) {
  .container, .container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
    max-width: calc(#{$content-width-xxl} + (var(--bs-gutter-x) * 2));
  }
}

//3XL
@media only screen and (min-width: $breakpoint-3xl) {
  .container, .container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
    max-width: calc(#{$content-width-3xl} + (var(--bs-gutter-x) * 2));
  }
}

//4XL
@media only screen and (min-width: $breakpoint-4xl) {
  .container, .container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
    max-width: calc(#{$content-width-4xl} + (var(--bs-gutter-x) * 2));
  }
}




