  margin-bottom: 20px;
  padding: 1rem 1rem;
  border-radius: 24px;
  border-width: 0;
  background: #3faccf;
  color: #FFF;
  text-shadow: 0 0 1px #369, 0 1px 1px #187593;
  font-weight: 600;
}

.applications {
  dt {
    display: inline-block;
    font-size: 1.5rem;
    letter-spacing: -1px;
    text-transform: uppercase;
    margin-bottom: 1rem;
  }
