/* ==========================================================================
    HOME CAROUSEL
   ========================================================================== */
.home-carousel {
  height: 400px;

  .carousel-inner,
  .carousel-item,
  .container {
    height: 100%;
  }

  .container {
    position: relative;
  }
  .carousel-caption {
    position: relative;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding-top: 3rem;
    padding-bottom: 1.25rem;
    text-align: left;
    height: 100%;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    z-index: 1;
  }
}

.carousel-item {
  position: relative;
}

.carousel-item figure {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  margin: 0;
}
.carousel-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.carousel-caption,
.page-banner-inner {
  text-shadow: 2px 2px 4px rgba(0,0,0,0.5);
}

@media only screen and (min-width: 768px) {
  .home-carousel {
    height: 400px;
    .lead{
      max-width: 720px;
    }
  }
  .carousel-caption,
  .page-banner-inner {
    text-shadow: 2px 2px 4px rgba(0,0,0,0.9);
  }
}
@media only screen and (min-width: 1200px) {
  .home-carousel {
    height: 600px;
  }
}
@media only screen and (min-width: 1600px) {
  .home-carousel {
    height: 700px;
  }
}
